
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;
  -moz-font-feature-settings: "liga" on;
}
body, .app {
  width: 100%;
}
h1, h2, h3, h4, h5 {
  font-weight: 200;
}
.links {
  position: fixed;
  top: 50vh;
}
li {
  list-style: none;
  margin-bottom: 10px;
}
li a {
  font-size: 16px;
  color: #939393;
  font-family: monospace;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}
li:hover a {
  color: #262626;
  font-size: 18px;
}
header {
  padding-top: 10vh;
  text-align: center;
}
.title {
  font-family: serif;
}
h1 {
 font-size: 72px;
}
h2 {
  font-size: 20px;
  margin: 0 0 10px 0;
}
.container {
  width: 100%;
}
.projects {
  width: 60%;
  margin: 10vh auto 0 auto;
}
.project {
  border-radius: 5px;
  margin-bottom: 10vh;
}
.project-title {
  font-size: 25px;
  font-weight: bolder;
}
.project-bio {
  color: #939393;
  font-weight: 200;
}
.project-link {
  color: #000;
  font-style: italic;
  text-decoration: none;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}
.project-badges {
  padding: 5px;
  border-bottom: 1px solid #939393;
}
.project-image img {
  margin-top: 5px;
  width: 100%;
}



